import React from "react"
import "@web/assets/App.scss"
import Header from "@web/components/header/header"
import Pic from "@web/components/content/picture"
import Info from "@web/components/content/info"
import Thanks from "@web/components/content/thanks"
import EachCard from "@web/components/content/eachCard"
import WeddingTime from "@web/components/content/weddingTime"
import WeddingAddress from "@web/components/content/weddingAddress"
import CountDown from "@web/components/content/countDown"
import Footer from "@web/components/footer/footer"
import { Parallax, ParallaxLayer } from "@react-spring/parallax"
import { Grid } from "antd-mobile"

function App() {
    const contentList = [<Pic />, <WeddingTime />, <Info />, <Thanks />, <WeddingAddress />, <CountDown />]
    return (
        <div className="App">
            {/* header */}
            <Grid columns={1} gap={1} className="Header">
                <Header />
            </Grid>
            {/* content */}
            <Grid columns={1} gap={1}>
                <Parallax pages={contentList.length} style={{ top: "0", left: "0" }} className="Content">
                    {contentList.map((content, index) => {
                        return (
                            <React.Fragment key={index}>
                                <ParallaxLayer offset={index} speed={0.8}>
                                    <div className={"slopeBegin"} />
                                </ParallaxLayer>

                                <ParallaxLayer offset={index} speed={0.6}>
                                    <div className={"slopeEnd"} />
                                </ParallaxLayer>
                                <ParallaxLayer
                                    key={index}
                                    offset={index}
                                    speed={0.2}
                                    style={{
                                        display: "flex",
                                        justifyContent: "center",
                                        alignItems: "center",
                                    }}
                                >
                                    <EachCard key={index}>{content}</EachCard>
                                </ParallaxLayer>
                            </React.Fragment>
                        )
                    })}
                </Parallax>
            </Grid>
            {/* footer */}
            <Grid columns={1} gap={1} className="Footer">
                <Footer />
            </Grid>
        </div>
    )
}

export default App
