import React from "react"
import { Badge, TabBar, Toast } from "antd-mobile"
import { AppOutline, MessageOutline, MessageFill, UnorderedListOutline, UserOutline } from "antd-mobile-icons"
import moment from "moment"

const message = (str: any) => {
    Toast.show(str)
}

const Footer = () => {
    const [title] = React.useState<string>("bottom")
    const [year, setYear] = React.useState<string>(moment().format("YYYY"))
    const tabs = [
        {
            key: "copyright",
        },
    ]

    return (
        <TabBar>
            {" "}
            {tabs.map((item) => (
                <TabBar.Item
                    key={item.key}
                    title={"Copyright©" + year + "Arther &Ninny All rights reserved"}
                    style={{ color: "#EAEAEA" }}
                />
            ))}
        </TabBar>
    )
}

export default Footer
