import React from "react"
import { useSpring, animated, config } from "react-spring"
import { Grid } from "antd-mobile"
import Typist from "react-typist"
import { HeartOutline } from "antd-mobile-icons"
import "@web/components/content/weddingTime.scss"
const NinnyLove = () => {
    return <HeartOutline style={{ fontSize: "60px" }} color="#eb2f96" />
}

const WeddingTime = () => {
    const WeddingTimeInfo = " 2021/9/18"
    const cursorConfig: Typist.CursorProps = {
        hideWhenDone: true,
    }
    return (
        <>
            <div>
                <Typist.Delay ms={5000} />
                <Typist cursor={cursorConfig} className={"WeddingTimeTitle"}>
                    Wedding Information
                </Typist>
            </div>
            <Grid columns={5} gap={8}>
                <Grid.Item span={2}>
                    <Typist cursor={cursorConfig}>
                        <Typist cursor={cursorConfig} className={"WeddingTimeTitle"}>
                            時間
                        </Typist>
                        <Typist.Delay ms={3000} />
                        <NinnyLove />
                        <Typist cursor={cursorConfig} className={"WeddingTimeContent"}>
                            {WeddingTimeInfo}
                        </Typist>
                        <Typist cursor={cursorConfig} className={"WeddingTimeContent"}>
                            4:00 pm
                        </Typist>
                    </Typist>
                </Grid.Item>
                <Grid.Item />
                <Grid.Item span={2}>
                    <Typist cursor={cursorConfig}>
                        <Typist cursor={cursorConfig} className={"WeddingTimeTitle"}>
                            地點
                        </Typist>
                        <Typist.Delay ms={3000} />
                        <NinnyLove />
                        <Typist cursor={cursorConfig} className={"WeddingTimeContent"}>
                            好運來洲際宴展中心
                        </Typist>
                    </Typist>
                </Grid.Item>
            </Grid>
        </>
    )
}
export default WeddingTime
