import React from "react"
import { useSpring, animated, config } from "react-spring"
import { Grid } from "antd-mobile"
import Typist from "react-typist"
import "@web/components/content/countDown.scss"
import { CountdownCircleTimer } from "react-countdown-circle-timer"
import Clock from "react-live-clock"
import "moment-timezone"
import "react-moment"
const timerProps = {
    isPlaying: true,
    size: 90,
    strokeWidth: 6,
}

const minuteSeconds = 60
const hourSeconds = 3600
const daySeconds = 86400

const renderTime = (dimension: string, time: number) => {
    return (
        <div className="Circle">
            <div className="Circle">{time}</div>
            <div>{dimension}</div>
        </div>
    )
}

const getTimeSeconds = (time: number) => Math.ceil(minuteSeconds - time) - 1
const getTimeMinutes = (time: number) => Math.ceil(time / minuteSeconds) - 1
const getTimeHours = (time: number) => Math.ceil(time / hourSeconds) - 1
const getTimeDays = (time: number) => Math.ceil(time / daySeconds) - 1

const AC = "#006699"
const BC = "#99CCCC"
const CC = "#CCCCCC"

const Render = ({ endTime }: { endTime: number }) => {
    const now = Date.now() / 1000
    const remainingTime = endTime / 1000 - now
    console.log(endTime, now)
    const days = Math.ceil(remainingTime / daySeconds)
    const daysDuration = days * daySeconds
    return (
        <div className={"Display"}>
            <CountdownCircleTimer
                {...timerProps}
                colors={[
                    [AC, 0],
                    [BC, 0.5],
                    [CC, 1],
                ]}
                isLinearGradient={true}
                duration={daysDuration}
                initialRemainingTime={remainingTime}
                trailColor={"#dbdbdb"}
            >
                {({ elapsedTime }) => renderTime("天", elapsedTime ? getTimeDays(daysDuration - elapsedTime) : 0)}
            </CountdownCircleTimer>
            <CountdownCircleTimer
                {...timerProps}
                colors={[
                    [AC, 0],
                    [BC, 0.5],
                    [CC, 1],
                ]}
                isLinearGradient={true}
                duration={daySeconds}
                initialRemainingTime={remainingTime % daySeconds}
                onComplete={(totalElapsedTime) => [remainingTime - totalElapsedTime > hourSeconds, 60]}
                trailColor={"#dbdbdb"}
            >
                {({ elapsedTime }) => renderTime("時", elapsedTime ? getTimeHours(daySeconds - elapsedTime) : 0)}
            </CountdownCircleTimer>
            <CountdownCircleTimer
                {...timerProps}
                colors={[
                    [AC, 0],
                    [BC, 0.5],
                    [CC, 1],
                ]}
                isLinearGradient={true}
                duration={hourSeconds}
                initialRemainingTime={remainingTime % hourSeconds}
                onComplete={(totalElapsedTime) => [remainingTime - totalElapsedTime > minuteSeconds, 60]}
                trailColor={"#dbdbdb"}
            >
                {({ elapsedTime }) => renderTime("分", elapsedTime ? getTimeMinutes(hourSeconds - elapsedTime) : 0)}
            </CountdownCircleTimer>
            <CountdownCircleTimer
                {...timerProps}
                colors={[
                    [AC, 0],
                    [BC, 0.5],
                    [CC, 1],
                ]}
                isLinearGradient={true}
                duration={minuteSeconds}
                initialRemainingTime={remainingTime % minuteSeconds}
                onComplete={(totalElapsedTime) => [remainingTime - totalElapsedTime > 0, 60]}
                trailColor={"#dbdbdb"}
            >
                {({ elapsedTime }) => renderTime("秒", elapsedTime ? getTimeSeconds(elapsedTime) : 0)}
            </CountdownCircleTimer>
        </div>
    )
}

const CountDown = () => {
    const cursorConfig: Typist.CursorProps = {
        hideWhenDone: true,
    }
    const weddingTime = new Date("2022/3/26 12:00:00")
    console.log(weddingTime)
    return (
        <>
            <h3>
                <Clock className="clock" ticking={true} date={weddingTime.toString()} format={" DD MMMM YYYY, HH:mm"} />
            </h3>
            <div style={{ padding: "1px" }}>
                <Render endTime={weddingTime.valueOf()} />
            </div>
        </>
    )
}
export default CountDown
