import React from "react";
import { Card, Divider } from "antd-mobile";
import "@web/components/content/eachCard.scss";
const EachCard = ({ children }: { children: any }) => {
  const width = "100%";
  const height = "250px";
  return (
    <>
      <Divider />
      <Card className={"Card"}>{children}</Card>
    </>
  );
};

export default EachCard;
