import React from "react"
import { Image, Toast } from "antd-mobile"
// import logo from "@web/assets/logo.png";
import { Swiper, SwiperSlide } from "swiper/react/swiper-react.js"
import SwiperCore, { Autoplay, Pagination, EffectCube } from "swiper"
import p1 from "@web/assets/1.jpg"
import p2 from "@web/assets/2.jpg"
import p3 from "@web/assets/3.jpg"
import p4 from "@web/assets/4.jpg"
import p5 from "@web/assets/5.jpg"
import "swiper/swiper.scss" // core Swiper
import "swiper/modules/effect-cube/effect-cube.scss" // Navigation module
import "swiper/modules/pagination/pagination.scss" // Pagination module

SwiperCore.use([Autoplay, EffectCube, Pagination])

const Picture = () => {
    let logo =
        "https://www.poaipets.com.tw/wp-content/uploads/2021/03/%E5%89%96%E6%9E%90%E7%8B%97%E7%8B%97%E5%B8%B8%E8%A6%8B%E7%96%BE%E7%97%85-800x480.jpg"
    let width = "100%"
    const message = (str: any) => {
        Toast.show(str)
    }

    const list = [
        {
            message: "123",
            img: p1,
        },
        {
            message: "123",
            img: p2,
        },
        {
            message: "123",
            img: p3,
        },
        {
            message: "123",
            img: p4,
        },
        {
            message: "123",
            img: p5,
        },
    ]
    return (
        <Swiper
            effect={"cube"}
            grabCursor={true}
            cubeEffect={{
                shadow: true,
                slideShadows: true,
                shadowOffset: 20,
                shadowScale: 0.94,
            }}
            pagination={true}
            className="mySwiper"
            autoplay={{
                delay: 2500,
                disableOnInteraction: false,
            }}
        >
            {list.map((each, index) => {
                return (
                    <SwiperSlide
                        key={index}
                        onClick={() => {
                            message(each.message)
                        }}
                    >
                        <img src={each.img} width={width} key={index}></img>
                    </SwiperSlide>
                )
            })}
        </Swiper>
    )
}

export default Picture
